import UserInfo from '../../components/userinfo.vue';

export default {
	components: {
		UserInfo,
	},

	data() {
		return {
			doctorInfo: {
				avatarUrl: '',
				doctorName: '',
				amount: 0,
				states: '',
				bankList: []
			},
			isSetPassword: false
		}
	},
	onShow() {
		uni.setNavigationBarColor({
			frontColor: '#ffffff',
			backgroundColor: '#00C694',
		})
		this.doctorInfo = uni.getStorageSync("doctorInfo");
		if (!this.$util.isEmpty(this.doctorInfo.avatarUrl)) {
			this.doctorInfo.avatarUrl = '../../static/avatar.png';
		}
		if (!this.$util.isEmpty(this.doctorInfo.doctorName)) {
			this.doctorInfo.doctorName = '';
		}
		if (this.$util.isEmpty(this.doctorInfo.withdrawPassword)) {
			this.isSetPassword = true;
		}
	},
	methods: {
		pdLogin: function() {
			uni.navigateTo({
				url: '/pages/my/userDetail'
			})
		},
		setPassword: function() {
			let that = this;
			if (that.$util.isPerfectInfo()) {
				uni.navigateTo({
					url: '/pages/my/userPassword'
				})
			}
		},
		resetPassword: function() {
			let that = this;
			that.$util.request({
				url: "/didi-doctor/doctorresetinfo/resetPassword",
				param: {},
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.showToast({
						title: '申请成功',
						icon: 'success',
						duration: 2000
					})
				},
				error: function() {}
			})
		},
		toBankInfo: function() {
			let that = this;
			if (that.$util.isPerfectInfo()) {
				uni.navigateTo({
					url: '/pages/my/userBank'
				})
			}
		},
		toUserAmount: function() {
			if(this.doctorInfo.amount <= 0){
				uni.showToast({
					title: '余额为0',
					icon: 'success',
					duration: 2000
				})
				return;
			}
			let that = this;
			if (that.$util.isPerfectInfo()) {
				uni.navigateTo({
					url: '/pages/my/userAmount?amount=' + this.doctorInfo.amount
				})
			}
		},
		toMyRecept: function() {
			let that = this;
			if (that.$util.isPerfectInfo()) {
				uni.navigateTo({
					url: '/pages/my/userRecept'
				})
			}
		},
		getBankList: function() {
			let that = this;
			that.$util.request({
				url: "/didi-doctor/doctorbankinfo/findList",
				param: {},
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					that.bankList = res.data;
					if (that.bankList == null || that.bankList.length == 0) {
						uni.showToast({
							title: '请添加银行卡',
							icon: 'success',
							duration: 2000
						})
						return;
					}
					uni.navigateTo({
						url: '/pages/my/userAgent?bankList=' + JSON.stringify(that.bankList)
					})
				},
				error: function() {}
			})
		},
		toAgent: function() {
			let that = this;
			// that.getBankList();
			that.$util.request({
				url: "/didi-doctor/agentinfo/applyToAgent",
				param: that.temp,
				contentType: 'application/x-www-form-urlencoded',
				success: function(res) {
					uni.showToast({
						title: '申请成功,等待审核',
						icon: 'success',
						duration: 2000
					})
				},
				error: function() {
					uni.showToast({
						title: res.message,
						icon: 'success',
						duration: 2000
					})
				}
			})
		},
		toDidiDetail: function() {
			uni.navigateTo({
				url: '/pages/my/didiDetail'
			})
		},
		toQrCode: function() {
			uni.navigateTo({
				url: '/pages/my/userQrCode'
			})
		},
		toMyLearn:function(){
			uni.navigateTo({
				url: '/pages/my/myLearn'
			})
		}
	}
}
